  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
  --front-primary: var(--black);
  --front-secondary: var(--grey);
  --front-tertiary: var(--blue-lighten);
  --front-invert: var(--white);
  --front-brand: var(--blue);
  --front-link: var(--blue-light);
  --front-error: var(--red);
  --back-primary: var(--white);
  --back-secondary: var(--blue-lighten);
  --back-light: var(--blue-light);
  --back-medium: var(--blue-medium);
  --back-dark: var(--blue);
  --back-circle-light: #83c7f8;
  --back-circle-medium: #61aee5;
  --back-circle-dark: #4393cd;
  --darker-bg-color: var(--black-transparent);
  --transition-default: 0.25s ease;
  --blur-default: blur(8px);
  --z-index-base: 1;
  --z-index-header: 2;
  --z-index-darker: 3;
  --z-index-nav: 4;
  --max-width: 1296px;
  --nav-width: 264px;
  --page-padding: 16px;
}
@media screen and (min-width: 768px) {
  :root {
    --page-padding: 24px;
  }
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}
body[data-state=nav-open] {
  overflow: hidden;
}
@media screen and (min-width: 1280px) {
  body[data-state=nav-open] {
    overflow: auto;
  }
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

.hystmodal__opened {
  overflow: hidden;
}

.hystmodal__shadow {
  background: var(--back-transparency);
  border: none;
  bottom: 0;
  display: block;
  left: 0;
  opacity: 0;
  overflow: hidden;
  pointer-events: none;
  position: fixed;
  right: 0;
  top: 0;
  transition: opacity 0.25s ease;
  width: 100%;
  z-index: 98;
}

.hystmodal__shadow--show {
  opacity: 1;
  pointer-events: auto;
  transition: opacity 0.25s;
}

.hystmodal {
  bottom: 0;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  left: 0;
  opacity: 1;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
  pointer-events: none;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
  z-index: 99;
}

.hystmodal--active {
  opacity: 1;
}

.hystmodal--moved,
.hystmodal--active {
  pointer-events: auto;
  visibility: visible;
}

.hystmodal__wrap {
  align-items: center;
  display: flex;
  flex-flow: column nowrap;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
  margin: auto;
  min-height: 100%;
  width: 100%;
  background-color: var(--darker-bg-color);
  backdrop-filter: var(--blur-default);
}

.hystmodal__window {
  box-sizing: border-box;
  flex-grow: 0;
  flex-shrink: 0;
  margin: 5rem 0;
  max-width: 100%;
  opacity: 0;
  overflow: visible;
  transform: scale(0.9);
  transition: transform 0.2s ease 0s, opacity 0.2s ease 0s;
}

.hystmodal--active .hystmodal__window {
  opacity: 1;
  transform: scale(1);
}

.hystmodal__close {
  background: transparent;
  border: none;
  cursor: pointer;
  display: block;
  font-size: 0;
  outline: none;
  padding: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 10;
}

